import Vue from 'vue'
import Router from 'vue-router'

import Home from '@/views/Home'
import Shop from '@/views/Shop'
import SingleProduct from '@/views/SingleProduct'
import Heirloom from '@/views/Heirloom'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: '/',
      component: Home
    },
    {
      path: '/shop',
      name: 'Shop',
      component: Shop
    },
    {
      path: '/SingleProduct',
      name: 'SingleProduct',
      component: SingleProduct
    },
    {
      path: '/Heirloom',
      name: 'Heirloom',
      component: Heirloom
    },
  ]
})



